import React, { Component } from 'react';
import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import { Alert, Col } from 'reactstrap';

import {
  submitContactAPI
} from '../../api/data';

class ContactForm extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      email: "",
      phone: "",
      formError: false,
      result: false,
      errorMessage: ""

    }
  }

  handleChange = async (event) => {
    const { target } = event;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const { name } = target;

    await this.setState({
      [name]: value,
    }, () => {

    })


  }

  getName = (e) => {
    let username = e.target.value;
    this.setState({
      name: username,
      formError: false,
      result: false,
    });
    console.log(this.state.name);
  }

  isNumeric(value) {
    return /^-?\d+$/.test(value);
  }

  getPhone = (e) => {
    var phone = e.target.value;
    if (this.isNumeric(phone) || phone ===""){
      this.setState({
        phone: phone,
        formError: false,
        result: false,
      });
    }else{
     
      this.setState({
        errorMessage: "Vui lòng nhập đúng số điện thoại",
        formError: true,
        result: true,
      });

    }

  }


  getEmail = (e) => {
    let email = e.target.value;
    this.setState({
      email: email,
      formError: false,
      result: false,
    });
    console.log(this.state.email);
  }


  submitForm = async (e) => {

    e.preventDefault();

    if (this.state.name === "" || this.state.phone === "") {

      this.setState({
        formError: true,
        result: true,
        errorMessage: "Vui lòng điền đầy đủ thông tin"
      })
      return false;
    } else {
      this.setState({
        formError: false,
        result: true
      })
      console.log(`UserData: {
            Username: ${this.state.name},
            Phone: ${this.state.phone}
            Email: ${this.state.email},
           
        }`)

      var result = await submitContactAPI({name: this.state.name, phone: this.state.phone, email: this.state.email}) 
      console.log("form sent")

    }

  }

  render() {

    var isShowAlertError = false
    var isShowAlertSucess = false
    if (this.state.formError && this.state.result) {
      isShowAlertError = true;
    }
    if (this.state.result && !this.state.formError) {
      isShowAlertSucess = true
    }

    return (
      <Form className="form-contact">
        
        <FormGroup row className="form-group-contact">
          <Label for="name" sm={3}>Họ tên</Label>
          <Col sm={9}>
            <Input type="text" name="name" id="name" value={this.state.name} onChange={this.getName} placeholder="Điền họ tên" />
          </Col>
        </FormGroup>
        <FormGroup row className="form-group-contact">
          <Label for="phone" sm={3}>SĐT</Label>
          <Col sm={9}>
            <Input type="text" name="phone" id="phone" value={this.state.phone} onChange={this.getPhone} placeholder="Điền số điện thoại" />
          </Col>
        </FormGroup >
        <FormGroup row className="form-group-contact">
          <Label for="email" sm={3}>Email</Label>
          <Col sm={9}>
            <Input type="email" name="email" id="email" value={this.state.email} onChange={this.getEmail} placeholder="Điền email" />
          </Col>
        </FormGroup>
        <FormGroup row className="form-group-contact">
        {isShowAlertError ?
          <Alert color="danger">
            {this.state.errorMessage}
          </Alert>
          : null}
        {isShowAlertSucess ?
          <Alert color="success">
            Đã gửi thông tin thành công. Toangroi.com sẽ liên lạc với bạn trong vòng 24h
          </Alert>
          : null}

          <Button color="secondary" onClick={this.submitForm}>Gửi thông tin</Button>
        </FormGroup>
        {/*
      
        <form>
          
              {this.state.formError &&
                <p className="error">
                    Vui lòng điền đầy đủ thông tin
                </p>
              }
             
              <div>
                <label htmlFor="name">Họ tên</label>
                <input type="text" name="name" placeholder="" onChange={this.getName} />
              </div>
              <div>
                <label htmlFor="phone">SĐT/Zalo</label>
                <input type="text" name="phone" placeholder="" onChange={this.getName} />
              </div>
              <div>
                <label htmlFor="email">Email (nếu có)</label>
                <input type="email" name="email" placeholder="" onChange={this.getEmail} />
              </div>
             
              <div>
                <p>Trong vòng 24h, chúng tôi sẽ liên hệ với bạn</p>
                <input type="submit" name="submit" value="Send" onClick= {this.submitForm} />

              </div>
              
            </form>
            */}
      </Form>


    );
  }
}

export default ContactForm;